import './global.css'
import './cursor.scss'
import React, { useState, useEffect } from 'react'
import MouseFollower from 'mouse-follower'
import gsap from 'gsap'
import image1 from './assets/image1.jpeg'
import image2 from './assets/image2.jpeg'
import image3 from './assets/image3.jpeg'
import image4 from './assets/image4.jpeg'
import image5 from './assets/image5.jpeg'
import image6 from './assets/image6.jpeg'

const images = [image1, image2, image3, image4, image5, image6]

MouseFollower.registerGSAP(gsap)
const cursor = new MouseFollower()

const Loading = ({ calculatedWidth }) => (
	<aside>
		<div className="loading-bar">
			<label htmlFor="images-loaded">Loading all your favorite images...</label>
			<progress id="images-loaded" max="100" value={calculatedWidth}></progress>
		</div>
	</aside>
)

const App = () => {
	useEffect(() => {
		const onHeader = document.querySelector('header')
		onHeader.addEventListener('mouseenter', () => {
			cursor.setSkewing(3)
		})
		onHeader.addEventListener('mouseleave', () => {
			cursor.removeSkewing()
		})
		const onFigure = document.querySelector('figure')
		onFigure.addEventListener('mouseenter', () => {
			cursor.setText('Click!')
		})
		onFigure.addEventListener('mouseleave', () => {
			cursor.removeText()
		})
	})

	const [currentImage, setCurrentImage] = useState(0)
	const [numLoaded, setNumLoaded] = useState(0)

	const handleClick = () => {
		const length = images.length - 1

		setCurrentImage((currentImage) =>
			currentImage < length ? currentImage + 1 : 0
		)
	}

	const handleImageLoad = () => {
		setNumLoaded((numLoaded) => numLoaded + 1)
	}

	return (
		<section>
			<header>
				<h1>Zesty</h1>
				<h2>
					A photography project <br /> by Ella Fieldling
				</h2>
			</header>

			<figure>
				{numLoaded < images.length ? (
					<Loading calculatedWidth={(numLoaded / images.length) * 100} />
				) : null}
				<figcaption>
					{currentImage + 1} / {images.length}
				</figcaption>
				{images.map((imageURL, index) => (
					<img
						alt=""
						key={imageURL}
						src={imageURL}
						onClick={handleClick}
						onLoad={handleImageLoad}
						// style={{ opacity: currentImage === index ? 1 : 0 }}
						className={currentImage === index ? 'display' : 'hide'}
					/>
				))}
			</figure>
		</section>
	)
}

export default App
